import { assign } from '../util/ObjectUtils';

const authReducer = (state, action) => {
  const { type } = action;
  switch (type) {
    case 'REQUEST_REGISTER_PAGE':
      return assign(state, { authInfo: { logged: false } });
    case 'SHOW_USER_MENU':
      return assign(state, { authInfo: { logged: true } });
    default:
      return assign(state);
  }
};

export default authReducer;

export const getAuthInfoSelector = (stateAuthInfo) => {
  // do some filtering of state and
  // return new state
  console.log(stateAuthInfo);
};
